import React, {useState, useEffect} from 'react';
import { StyleSheet, Text, View, SafeAreaView, FlatList, ActivityIndicator} from 'react-native';
import DatePicker from 'react-native-datepicker';
import Logo from './components/Logo';
import MyComponent from './components/HeaderTitle';
import moment from 'moment';

import {
  createAppContainer,
  createBottomTabNavigator,
  createStackNavigator,
} from 'react-navigation';
import { TouchableOpacity } from 'react-native-gesture-handler';


const EasyRentURL = "https://easyrent-api-dev.cit362.com/reservations"; 
const App = () => {
  const [isLoading, setLoading] = useState(true);
  const [data, setData] = useState([]);
  const [title, setTitle] = useState([]);
  const [description, setDescription] = useState([]);
  const [date, setDate] = useState(moment());

  useEffect(() => {
    fetch(EasyRentURL)
      .then((response) => response.json())
      .then((json) => {
        setData(json);
      })
      .catch((error) => alert(error)) 
      .finally(() => setLoading(false));
  }, []);


  return (
    
    <SafeAreaView style={styles.container}>
      {isLoading ? (
        <ActivityIndicator/> 
      ) : (
      <View> 
      <MyComponent/> 
      
      <View style={styles.container}>
        <Text style={styles.title}>
          Due Date Reservations
        </Text>
        <DatePicker
          style={styles.datePickerStyle}
          date={date} //initial date from state
          mode="date" //The enum of date, datetime and time
          placeholder="select date"
          format="DD-MM-YYYY"
          minDate="01-01-2016"
          maxDate="01-01-2090"
          confirmBtnText="Confirm"
          cancelBtnText="Cancel"
          customStyles={{
            dateIcon: {
              //display: 'none',
              position: 'absolute',
              left: 0,
              top: 4,
              marginLeft: 0,
              
            },
            dateInput: {
              marginLeft: 36,
            },
          }}      
          onDateChange={(date) => {
            setDate(date);
          }}
        />
        <Text style={styles.title}>{title}</Text>
          <View style={{borderBottomWidth: 1, marginBottom: -5}}></View>

          <FlatList 
            data={data} 
            keyExtractor={({ id }, index) => id}
            renderItem={({ item }) => (
              <View style={styles.containerOne}>
                <TouchableOpacity style={styles.card}
                  onPress={()=> alert('Due date')}
                  >
                <Text style={styles.titleOne}>
                  {item.reservationId}. {item.customerId}
                </Text>
                </TouchableOpacity>
              </View>
            )}
          />
      </View>
      </View> 
      )}
    </SafeAreaView>
  );
};

export default App;

